import {
    CREATE_ORDER_TRANSACTION_REQUEST,
    CREATE_ORDER_TRANSACTION_SUCCESS,
    CREATE_ORDER_TRANSACTION_FAILURE,
} from '../../types/types';

const initialState = {
    loading: false,
    payload: [],
    error: '',
};

const createOrderTransactionReducer = (state = initialState, action) => {
    switch (action.type) {
        case CREATE_ORDER_TRANSACTION_REQUEST:
            return {
                loading: true,
                payload: [],
                error: '',
            };
        case CREATE_ORDER_TRANSACTION_SUCCESS:
            return {
                loading: false,
                payload: action.payload,
                error: '',
            };
        case CREATE_ORDER_TRANSACTION_FAILURE:
            return {
                loading: false,
                payload: [],
                error: action.error,
            };
        default:
            return state;
    }
};

export default createOrderTransactionReducer;
